<template>
  <div class='news'>
    <b-container>
      <b-row>
        <b-col>
            <div class="sub-title">
                <h1> Wiadomości </h1>
            </div>
        </b-col>
      </b-row>
        <b-row class="justify-content-end">
          <b-col>
            <div class="buttons mb-4">
              <button @click="changeCategory(null)" class="button mr-2 mb-2"> Ogólne </button>
              <button @click="changeCategory('business')" class="button ml-2 mr-2 mb-2"> Biznes </button>
              <button @click="changeCategory('entertainment')" class="button ml-2 mr-2 mb-2"> Rozrywka </button>
              <button @click="changeCategory('technology')" class="button ml-2 mr-2 mb-2"> Technologia </button>
              <button @click="changeCategory('sports')" class="button ml-2 mb-2"> Sport </button>
            </div>
          </b-col>
        </b-row>
      <b-row class="justify-content-center">
        <newsComponent v-for="news in newsData"
          :key="news.id"
          :title="news.title"
          :urlToImage="news.urlToImage"
          :text="news.description"
          :link="news.url"
          :content="news.content"
          > </newsComponent>
        </b-row>
    </b-container>
  <FooterComponent />
  </div>
</template>

<script>
import newsComponent from '../components/NewsComponent.vue'
import FooterComponent from '@/components/FooterComponent.vue'

export default {
    name: 'news',
    components: {
      newsComponent,
      FooterComponent
    },

    mounted() {
      this.fillNews();
    },

    data() {
      return {
        data: [],
        newsData: [],
        category: null,
      }
    },

    methods: {
      onSlideStart() {
        this.sliding = true
      },
      onSlideEnd() {
        this.sliding = false
      },
      // fill data
      async fillNews() {
        if(this.category === null) {
          const res = await fetch("data.json");
          const val = await res.json();
          this.data = [...val[0].articles];
          this.newsData = [...val[0].articles];
        } else if(this.category === 'business') {

          const res = await fetch("dataBusiness.json");
          const val = await res.json();
          this.data = [...val[0].articles];
          this.newsData = [...val[0].articles];
        } else if(this.category === 'entertainment') {

          const res = await fetch("dataEntertainment.json");
          const val = await res.json();
          this.data = [...val[0].articles];
          this.newsData = [...val[0].articles];
        } else if(this.category === 'technology') {
          
          const res = await fetch("dataTechnology.json");
          const val = await res.json();
          this.data = [...val[0].articles];
          this.newsData = [...val[0].articles];
        } else if(this.category === 'sports') {
          
          const res = await fetch("dataSports.json");
          const val = await res.json();
          this.data = [...val[0].articles];
          this.newsData = [...val[0].articles];
        }
      },

      async changeCategory(category) {
        this.category = category;
        this.fillNews();
      }
    }
}
</script>

<style scoped>
.news {
  padding: 0 !important;
  margin: 0 !important;
  width: 100%;
}

.sub-title {
  background-color: #00a2ff2a;
  border-radius: 15px;
  /* display: block; */
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 130px; margin-bottom: 10px;
  letter-spacing: 2px;
}

