.button {
  position: relative;
  overflow: hidden;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 10rem;
  height: 2.5rem;
  padding: 0.5rem;
  font-family: inherit;
  font-size: 1.1rem;
  border: 0;
  border-radius: 0.5rem;
  background: transparent;
  color: #f7f7f7;
  cursor: pointer;
  outline: none;
  transition-property: background, box-shadow;
  transition-duration: 0.35s;
  background: #5d2fdf;
}

.button:hover {
  background-color: #5526c5;
}
</style>